

.loginScreenForm {
    font-family: 'Arial', sans-serif;
    background: #fff;
    padding: 30px 40px;
    border-radius: 8px;
    width: 30%;
    box-shadow: 0px 15px 55px rgba(0, 0, 0, 0.2);
    text-align: center;
    margin: auto;
    margin-top: 5%;
    margin-bottom: 7%
}

.loginFieldLabel {
    display: block;
    font-weight: bold;
    margin-bottom: 8px;
    text-align: center;
}

.loginInput {
    width: 100%;
    padding: 12px 15px;
    margin-bottom: 20px;
    border: 1px solid #ccc;
    border-radius: 5px;
    font-size: 16px;
}
.loginButton {
    width: 100%;
    padding: 12px 15px;
    border: none;
    background-color: #213555; /* Button color matching the body background */
    color: #fff;
    font-size: 16px;
    border-radius: 5px;
    cursor: pointer;
    font-weight: 900;
    transition: background-color 0.1s ease-in;
    font-family: 'Sofia Sans Condensed', sans-serif;
}

.loginButton:hover {
    background-color: #E5D283; /* Change background color on hover */
    color: #100F0F;
}
